import React from 'react';
import { motion } from 'framer-motion';
import { NavLink } from 'react-router-dom';
import react from '../../assets/skills icons/react.png';
import js from '../../assets/skills icons/js.png';
import fb from '../../assets/skills icons/fb.png';
import html from '../../assets/skills icons/html.png';
import css from '../../assets/skills icons/css.png';

const componentVariants = {
  hidden: { x: -2000, originX: 0, scale: 0 },
  visible: { x: 0, scale: 1, transition: { delay: .5, type: 'spring', damping: 15 } },
  exit: { x: 2000, originX: 0, scale: 0, transition: { duration: 0.5 } }
}

const pVariants = {
  hidden: { scale: 0 },
  visible: { scale: 1, transition: { delay: 1.1, type: 'spring' } },
  hover: { scale: 0.8, originX: 0, transition:{ type: 'spring', stiffness: 300, damping: 5 } }
}

const imgContainerVariants = {
  hidden: { scale: 0 },
  visible: { scale: 1, transition: { delayChildren: 1.7, staggerChildren: .4, } }
}

const imageVariants = {
  hidden: { scale: 0 },
  visible: { scale: 1, transition: { type: 'spring', damping: 15 } }
}

const Skills = () => {
  return (
    <motion.div className="home" variants={componentVariants} initial='hidden' animate='visible' exit='exit'>
      <p className="tag h2-tag">&lt;h1&gt;<span className="title-str">&nbsp;Skills&nbsp;</span>&lt;/h1&gt;</p>
      <motion.p className="tag p-tag" variants={pVariants} initial='hidden' animate='visible'>
        &lt;p&gt;<span className="content-str">
        &nbsp; The main area of my expertise is front end development. Hard skills in HTML, CSS and JavaScript. I prefer creating small and medium web apps with responsive and animated layouts using ReactJS as front end and Firebase as back end. <br/>
        Check out my resume below for more details or just <NavLink to="/contact" className="amber-text text-darken-4">contact me</NavLink>. &nbsp;</span>&lt;/p&gt;
      </motion.p>
      <motion.div variants={imgContainerVariants} className="skills-img-container">
        <motion.img variants={imageVariants} src={react} alt="themeImg"/>
        <motion.img variants={imageVariants} src={js} alt="themeImg"/>
        <motion.img variants={imageVariants} src={fb} alt="themeImg"/>
        <motion.img variants={imageVariants} src={html} alt="themeImg"/>
        <motion.img variants={imageVariants} src={css} alt="themeImg"/>
      </motion.div>
    </motion.div>
  );
}
 
export default Skills;